body {
    font-family: "Helvetica Neue", Helvetica, "Segoe UI", Arial, sans-serif;
}

a {
  color: tomato;
  //color: slategrey;
  background: transparent;
  word-wrap: break-word;
  word-break: break-all;
  text-decoration: none;
}

a:active, a:hover {
    color: cornflowerblue;
    outline: 1;
}

li a{
    color: slategrey;
}

li a:active, a:hover{
    color: cornflowerblue;
}


h1, h2, h3, h4, h5, h6 {
  margin-top: 12px;
  margin-bottom: 12px;
  line-height: 1.1;
}

nav {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0%);
}

nav ul {
    width: 500px;
    font-size: 18px;
    list-style-type: none;
    font: Monaco, Consolas, "Andale  Mono", "DejaVu Sans Mono", monospace;
}

nav li {
    width: 200px;
    float: left;
    padding: 0% 5% 0% 5%;
}

nav a {
    padding: 5px;
    border-radius: 5px; 
    -moz-border-radius: 5px; 
    -webkit-border-radius: 5px; 
    display: block;
    text-align: center;
}

.current {
    border: 1px solid #BCBEC0;
}

h1 {
    color: slategrey;
    text-align: center;
    font-size: 2.25em;
    padding-bottom: 0.3em;
}

h2 {
    margin: 1em 0em 1em 0em;
    padding-bottom: 0.3em;
    font-size: 1.75em;
    line-height: 1.225;
}

h3 {
  font-size: 1.5em;
  line-height: 1.43;
}

h4 {
  font-size: 1.25em;
  line-height: 1.2;
}

h5 {
  font-size: 1em;
}

h6 {
  font-size: 1em;
  color: #777;
}

code {
    border-radius: 5px; 
    -moz-border-radius: 5px; 
    -webkit-border-radius: 5px; 
    border: 1px solid #BCBEC0;
    padding: 2px;
    font:12px Monaco,Consolas,"Andale  Mono","DejaVu Sans Mono",monospace;
    line-height: 2;
    color: teal;
}

blockquote {
    position: relative;
    padding-left: 1em;
    border-left: 0.2em solid teal;
    font:16px Monaco,Consolas,"Andale  Mono","DejaVu Sans Mono",monospace;
    font-weight: 100;
    &:before, &:after {
        content: '\201C';
        font-family: 'Sanchez';
        color: teal;
    }
    &:after { content: '\201D'; }
}

.main{
    top: 80px;
}

.column {
    float: left;
}

.left {
    width: 20%;
    margin-right: 5%;
}

.right {
    width: 70%;
}

.fig_left {
  width: 45%;
  margin-right: 5%;
}

.fig_right {
  width: 45%;
  margin-left: 5%;
}

.intro{
    font-size: 16px;
    line-height: 1.5;
    padding: 12px;
    text-align: center;
    color: #333;
    font-family: monospace;
    border-radius: 5px; 
    -moz-border-radius: 5px; 
    -webkit-border-radius: 5px; 
    border: 1px solid #BCBEC0;
}

.post {
    color: #333;
    font-family: "Helvetica Neue", Helvetica, "Segoe UI", Arial, sans-serif;
    font-size: 16px;
    line-height: 1.5;
}

.centre{
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0%);
}

.catalog {
    list-style-type: none;
}

img {
    padding: 5%;
    text-align: center;
    width: 60%;
}

#side_bar li{
    margin: 0;
    padding: 0;
    list-style-type: none;
}

#side_bar ul{
    margin: 0;
    padding: 0%;
    padding-left: 10%;
    list-style-type: none;
}

